import React from "react";
import { Outlet } from "react-router-dom";

import Footer from "../footer";
import Header from "../header";
import styles from "./Index.module.css"

function Layout() {
    return (
        <div>
            <Header/>
            <main className={styles.mHeight}>
                <Outlet />
            </main>
            <Footer/>
        </div>
    );
}

export default Layout;