import React from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import { useAuth } from "../auth/AuthContext";

function Header() {

  let location = useLocation();
  let user = window.sessionStorage.getItem("user");
  let auth = useAuth();
  let navigate = useNavigate();
 
  return (
    <div>
      <Navbar bg="light" expand="lg">
        <Container fluid>
          <Navbar.Brand href="/recruiter">Elihr</Navbar.Brand>
          <Navbar.Toggle aria-controls="navbarScroll" className="shadow-none" />
          <Navbar.Collapse id="navbarScroll">
            <Nav
              className="me-auto my-2 my-lg-0"
              style={{ maxHeight: '250px' }}
              navbarScroll
            >
              {location.pathname === "/"||"recruiter" ? "" : <Nav.Link href="/recruiter">Home</Nav.Link>}
              <Link to={"/recruiter/about"} className="text-decoration-none pt-2 pl-2 text-reset">About</Link>
              <NavDropdown title="Service" id="navbarScrollingDropdown">
                <Link to={"/recruiter/auth/signin"} className="text-decoration-none d-block text-reset ml-2">Recruiter</Link>
                <Link to={"/recruiter/auth/signin"} className="text-decoration-none d-block text-reset mr-2">Seeker</Link>
                <NavDropdown.Divider />
                <Link to={"/recruiter/career"} className="text-decoration-none d-block text-reset ml-2">Career</Link>
                <Link to={"/recruiter/blogs"} className="text-decoration-none  d-block text-reset ml-2">Blog</Link>
              </NavDropdown>
            </Nav>

            {!user ?<button className="btn btn-link text-decoration-none text-muted" onClick={() => navigate("/recruiter/auth/signin", {replace:true})}>Sign in</button>:""}
            {!user ? <Link to={"/recruiter/auth/signup"} className="text-muted p-2 text-decoration-none">Sign up</Link> : ""}
            {user ? (<Link to={"/recruiter/users"} className="text-muted p-2 text-decoration-none">Account</Link>) : ""}
            {user ? <button className="btn btn-link text-decoration-none text-muted" onClick={() => auth.signout(() => navigate("/recruiter", {replace:true}))}>Sign out</button>:""}

          </Navbar.Collapse>
        </Container>
      </Navbar>
    </div>
  )
}
export default Header;